.AbilityBase {
    grid-column: 1/1;
    grid-row: 4/4;
    padding: 3em;
    font-size: x-large;
    background-color: #9393ab5b;
    margin: 3em;
    border-radius: 3em;
}

.AbilityDetails {
    padding: 1em;
}


@media (min-width: 70em) {
    .AbilityBase {
        grid-column: 1/1;
        grid-row: 3/3;
        padding: 3em;
        font-size: medium;
        background-color: #9393ab5b;
        max-width: 70%;
        margin-top: auto;
        margin-left: auto;

        border-radius: 3em;
    }
}